___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

.App {
    font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Playfair Display', Georgia, serif;
}

button {
    cursor: pointer;
    border: none;
    outline: none;
}

button:focus {
    outline: 2px solid #D4AF37;
    outline-offset: 2px;
}

input:focus,
textarea:focus {
    outline: none;
}

img {
    max-width: 100%;
    height: auto;
    display: block;
}

.animate-pulse-slow {
    animation: pulse 3s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

@keyframes pulse {

    0%,
    100% {
        opacity: 1;
    }

    50% {
        opacity: 0.8;
    }
}

.transition-smooth {
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    background: #FFF8E1;
}

::-webkit-scrollbar-thumb {
    background: #8D6E63;
    border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
    background: #5D4037;
}`, "",{"version":3,"sources":["webpack://./src/App.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,yKAAyK;IACzK,mCAAmC;IACnC,sCAAsC;AAC1C;;AAEA;;;;;;IAMI,+CAA+C;AACnD;;AAEA;IACI,eAAe;IACf,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,0BAA0B;IAC1B,mBAAmB;AACvB;;AAEA;;IAEI,aAAa;AACjB;;AAEA;IACI,eAAe;IACf,YAAY;IACZ,cAAc;AAClB;;AAEA;IACI,yDAAyD;AAC7D;;AAEA;;IAEI;;QAEI,UAAU;IACd;;IAEA;QACI,YAAY;IAChB;AACJ;;AAEA;IACI,iDAAiD;AACrD;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA;IACI,mBAAmB;AACvB","sourcesContent":["* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\nhtml {\n    scroll-behavior: smooth;\n}\n\n.App {\n    font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;\n    -webkit-font-smoothing: antialiased;\n    -moz-osx-osx-font-smoothing: grayscale;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n    font-family: 'Playfair Display', Georgia, serif;\n}\n\nbutton {\n    cursor: pointer;\n    border: none;\n    outline: none;\n}\n\nbutton:focus {\n    outline: 2px solid #D4AF37;\n    outline-offset: 2px;\n}\n\ninput:focus,\ntextarea:focus {\n    outline: none;\n}\n\nimg {\n    max-width: 100%;\n    height: auto;\n    display: block;\n}\n\n.animate-pulse-slow {\n    animation: pulse 3s cubic-bezier(0.4, 0, 0.6, 1) infinite;\n}\n\n@keyframes pulse {\n\n    0%,\n    100% {\n        opacity: 1;\n    }\n\n    50% {\n        opacity: 0.8;\n    }\n}\n\n.transition-smooth {\n    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\n}\n\n::-webkit-scrollbar {\n    width: 10px;\n}\n\n::-webkit-scrollbar-track {\n    background: #FFF8E1;\n}\n\n::-webkit-scrollbar-thumb {\n    background: #8D6E63;\n    border-radius: 5px;\n}\n\n::-webkit-scrollbar-thumb:hover {\n    background: #5D4037;\n}"],"sourceRoot":""}]);
